import React from 'react';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';

import Grid from '@material-ui/core/Grid';
import LinkIcon from '@material-ui/icons/Link';
import RoomIcon from '@material-ui/icons/Room';
import InfoIcon from '@material-ui/icons/Info';
import ImportContactsIcon from '@material-ui/icons/ImportContacts';
import GlobalInfo from './popups/GlobalInfo';
import MapPopup from './popups/MapPopup';
import Links from './popups/Links';
import GuestInfo from './popups/GuestInfo';
import DocToUse from './popups/DocToUse';

import MenuBookIcon from '@material-ui/icons/MenuBook';
import VocabularyInfo from './popups/VocabularyInfo';

const useStyles = makeStyles((theme) => ({
	root: {
		position: "absolute",
		marginTop: "57vh",
		zIndex: 2001,
		marginLeft: "3.5vh"
	},
	button: {
		margin: "2px",
		fontSize: "2vh",
        padding: "1px 3px 1px 3px",
        borderRadius: "2px",
        backgroundColor: "#00A178"
	},
}));

export default function ButtonBox() {
	const classes = useStyles();
	const [open, setOpen] = React.useState(false);

	const handleClickOpenI = () => {
		setOpen(1);
	};

	const handleClose = () => {
		setOpen(false);
	};


	const handleClickOpenM = () => {
		setOpen(2);
	};

	const handleClickOpenL = () => {
		setOpen(3);
	};

	const handleClickOpenG = () => {
		setOpen(4);
	};

	const handleClickOpenW = () => {
		setOpen(5);
	};

	const handleClickOpenGuest = () => {
		setOpen(6);
	};

	return (
		<>
			<Grid
				className={classes.root}
				container
				direction="column"
				justify="space-between"
				alignItems="flex-start"
			>
				<Button
					variant="contained"
					color="default"
					className={classes.button}
					startIcon={<InfoIcon style={{ marginRight: "-7px", fontSize: "2.6vh" }}/>}
					onClick={handleClickOpenI}
					size="small"
				>
					Историческая справка
				</Button>
				<Button
					variant="contained"
					color="default"
					className={classes.button}
					startIcon={<LinkIcon style={{ marginRight: "-7px", fontSize: "2.6vh" }}/>}
					onClick={handleClickOpenL}
					size="small"
				>
					Полезные ссылки
				</Button>
				<Button
					variant="contained"
					color="default"
					className={classes.button}
					startIcon={<RoomIcon style={{ marginRight: "-8px", fontSize: "2.6vh" }}/>}
					onClick={handleClickOpenM}
					size="small"
				>
					Карта объекта
				</Button>
				<Button
					variant="contained"
					color="default"
					className={classes.button}
					startIcon={<MenuBookIcon style={{ marginRight: "-7px", fontSize: "2.6vh" }}/>}
					onClick={handleClickOpenW}
					size="small"
				>
					Словарь
				</Button>
				<Button
					variant="contained"
					color="default"
					className={classes.button}
					startIcon={<ImportContactsIcon style={{ marginRight: "-7px", fontSize: "2.6vh" }}/>}
					onClick={handleClickOpenGuest}
					size="small"
				>
					Руководство по использованию
				</Button>
				<Button
					variant="contained"
					color="default"
					className={classes.button}
					startIcon={<ImportContactsIcon style={{ marginRight: "-7px", fontSize: "2.6vh" }}/>}
					onClick={handleClickOpenG}
					size="small"
				>
					Гостю
				</Button>
			</Grid>
			<GlobalInfo open={open === 1} onClose={handleClose} style={{ position: "absolute", zIndex: 2002, marginLeft: "58vw" }}/>
			<MapPopup open={open === 2} onClose={handleClose} style={{ position: "absolute", zIndex: 2002, marginLeft: "58vw" }}/>
			<Links open={open === 3} onClose={handleClose} style={{ position: "absolute", zIndex: 2002, marginLeft: "58vw" }}/>
			<GuestInfo open={open === 4} onClose={handleClose} style={{ position: "absolute", zIndex: 2002, marginLeft: "58vw" }}/>
			<VocabularyInfo open={open === 5} onClose={handleClose} style={{ position: "absolute", zIndex: 2002, marginLeft: "58vw" }}/>
			<DocToUse open={open === 6} onClose={handleClose} style={{ position: "absolute", zIndex: 2002, marginLeft: "58vw" }}/>
		</>
  );
}