export default function VocabularyInfo(props) {
	const { onClose, open } = props;

	const classes = useStyles();

	const handleClose = () => {
	onClose();
	};

	return (
		<Dialog onClose={handleClose} className={classes.container} aria-labelledby="simple-dialog-title" open={open} style={{ zIndex: 2002 }}>
			<DialogTitle id="alert-dialog-slide-title" style={{ backgroundColor: "#FAEBD7" }}>{"СЛОВАРЬ ОСНОВНЫХ ПОНЯТИЙ"}</DialogTitle>
			<DialogContent dividers style={{ backgroundColor: "#FAEBD7", padding: '12px' }}>
				{words.map((value, index)=>{
					return (
						<>
							<Grid key={value.id} container spacing={3}>
								<Grid item xs style={{ fontWeight: "bold", padding: '15px 2px 15px 12px' }}>
									{value.word}
								</Grid>
								<Grid item xs={8} style={{ padding: '15px 4px 15px 2px' }}>
									{value.description}
								</Grid>
							</Grid>
							<Divider />
      					</>
					)}
				)}
			</DialogContent>
			<DialogActions style={{ backgroundColor: "#FAEBD7"}}>
				<Typography style={{
					margin: "4px",
					fontSize: "14pt"
				}}>
					&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;* Словарь составлен по материалам википедии.
				</Typography>
			</DialogActions>
		</Dialog>
	);
}

VocabularyInfo.propTypes = {
	onClose: PropTypes.func.isRequired,
	open: PropTypes.bool.isRequired,
};