export default class CustomCarousel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: {
                label: imagesBox[0].label,
                text: imagesBox[0].text,
                mp3: imagesBox[0].mp3,
                commonmp3: imagesBox[0].commonmp3,
                hide: imagesBox[0].hide,
                titleSrc: imagesBox[0].titleSrc,
                autoPlayInterval: imagesBox[0].autoPlayInterval ? imagesBox[0].autoPlayInterval: 4000,
                noBreakSound: imagesBox[0].noBreakSound
            },
            open: false,
            openCommonSound: false,
            play: false
        };
        this.handleChangeData = this.handleChangeData.bind(this);
        this.handleOpenInfo = this.handleOpenInfo.bind(this);
        this.handleCloseInfo = this.handleCloseInfo.bind(this);
        this.handleOpenCommonSound = this.handleOpenCommonSound.bind(this);
        this.handlePlay = this.handlePlay.bind(this);
        this.handlePause = this.handlePause.bind(this);
    }

    handleChangeData(value) {
        this.setState((state, props) => {
          return {
            data: {
                label: imagesBox[value.curIndex].label,
                text: imagesBox[value.curIndex].text,
                mp3: imagesBox[value.curIndex].mp3,
                commonmp3: imagesBox[value.curIndex].commonmp3,
                hide: imagesBox[value.curIndex].hide,
                titleSrc: imagesBox[value.curIndex].titleSrc,
                autoPlayInterval: imagesBox[value.curIndex].autoPlayInterval ? imagesBox[value.curIndex].autoPlayInterval : DEFAULT_INTERVAL,
                noBreakSound: imagesBox[value.curIndex].noBreakSound
            },
            openCommonSound: state.play ? true : false
        };
        });
    }

    handleOpenInfo() {
        this.setState({
            open: true,
            openCommonSound: false
        });
    }

    handleCloseInfo() {
        this.setState({
            open: false
        });
    }

    handleOpenCommonSound() {
        this.setState((state) => {
            return {
              openCommonSound: !state.openCommonSound,
              open: false
            };
        });
    }

    handlePlay() {
        this.setState({
            play: true,
            openCommonSound: true
        });
    }

    handlePause() {
        this.setState({
            play: false,
            openCommonSound: false
        });
    }

  render() {
    return (
      <>
        {this.state.data.titleSrc && (
          <div style={{ position: "absolute", zIndex: "2001", textAlign: "center", display: "inline-block", width: "100%", marginTop: "11vh" }}>
            <img src={this.state.data.titleSrc} alt="title" style={{ width: "57vw"}}/>
          </div>        
        )}
        <Grid
          style={{
            position: "absolute",
            marginTop: "10vh",
            zIndex: 2001
          }}
          container
          direction="row"
          justify="space-between"
          alignItems="flex-start"
        >
          <IconButton 
            variant="contained"
            color="default"
            style={{
              paddingTop: 0,
              paddingLeft: "1.1vw"
            }}
            onClick={this.handleOpenCommonSound}
          >
            <VolumeDownIcon style={{ color: "#00A178", fontSize: "12vh"}}/>
          </IconButton>
          <InfoIcon
            style={{
              marginTop: "4vw",
              opacity: this.state.data.hide ? "0%" : "100%",
              marginRight: "3vw", 
              fontSize: "6vh",
              color: "#00A178"
            }}
            onClick={this.state.data.hide ? null : this.handleOpenInfo}
          />
        </Grid>
        <Sound
          url={this.state.data.commonmp3 ? this.state.data.commonmp3 : this.state.data.mp3}
          playStatus={this.state.openCommonSound && this.state.data.commonmp3 ? Sound.status.PLAYING : Sound.status.STOPPED}
          playFromPosition={this.state.data.commonmp3 ? 300 : 10/* in milliseconds */}
          volume={100}
        />
        <DetailInfo 
          open={this.state.open} 
          onClose={this.handleCloseInfo} 
          text={this.state.data.text}
          style={{
            position: "absolute",
            zIndex: 2002
          }}
        />
        <Carousel 
              images={imagesBox} 
              style={{ height: "100%", width: "100%", position: "fixed"}}
              onIndexChange={this.handleChangeData}
              isMaximized={true}
              autoPlayInterval={this.state.data.autoPlayInterval}
              playIcon={<PlayArrowIcon style={{ color: "white", fontSize: "12vh" }} onClick={this.handlePlay}/>}
              pauseIcon={<PauseIcon style={{ color: "white", fontSize: "12vh" }} onClick={this.handlePause}/>}
        />
      </>
    );
  }
}