import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import Typography from '@material-ui/core/Typography';

import Dialog from '@material-ui/core/Dialog';

const useStyles = makeStyles((theme) => ({
	root: {
		'& > * + *': {
			marginLeft: theme.spacing(2),
		},
		margin: "4px",
		fontSize: "16pt",
		color: "green!important"
	},
  	container: {
    	marginLeft: "40vw"
  	}
}));

const allLinks = [
	{
		id: "1",
		link: "https://geocaching.su/?pn=101&cid=13594",
		label: "Святск: легенды и были",
		style: {color: "green"}
	},{
		id: "2",
		link: "http://belsmi.by/news/culture/25165/",
		label: `Проект "Жемчужины малой родины". Святск: возрождение красоты`,
		style: {color: "green"}
	},{
		id: "3",
		link: "https://ru.wikipedia.org/wiki/%D0%92%D0%BE%D0%BB%D0%BE%D0%B2%D0%B8%D1%87%D0%B8_(%D1%80%D0%BE%D0%B4)",
		label: "Воловичи",
		style: {color: "green"}
	},{
		id: "4",
		link: "https://ru.wikipedia.org/wiki/%D0%A1%D0%B2%D1%8F%D1%82%D1%81%D0%BA_(%D0%93%D1%80%D0%BE%D0%B4%D0%BD%D0%B5%D0%BD%D1%81%D0%BA%D0%B0%D1%8F_%D0%BE%D0%B1%D0%BB%D0%B0%D1%81%D1%82%D1%8C)",
		label: "Святск",
		style: {color: "green"}
	},{
		id: "5",
		link: "https://vetliva.by/belarus/what-to-see/dvortsovo-parkovyy-ansambl-valovichey-v-derevne-svyatsk/",
		label: "Палацава-паркавы ансамбль Валовічаў у вёсцы Свяцк",
		style: {color: "green"}
	},{
		id: "6",
		link: "https://www.youtube.com/watch?v=7j6cOYW8sj0",
		label: "Святск. Реконструкция дворцово-паркового ансамбля Воловичей",
		style: {color: "green"}
	},
];

export default function Links(props) {
	const { onClose, open } = props;

	const classes = useStyles();

	const handleClose = () => {
	onClose();
	};

	return (
		<Dialog onClose={handleClose} className={classes.container} aria-labelledby="simple-dialog-title" open={open} style={{ zIndex: 2002 }}>
			<div  
        style={{
          backgroundColor: "#FAEBD7"
        }}>
				<Typography style={{
					margin: "4px",
					fontSize: "16pt"
				}}>
					&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;При создании виртуального тура использовались следующие источники:
				</Typography>
				{allLinks.map((value, index)=>{
					return (
						<Typography className={classes.root} key={value.id}>
							<Link href={value.link} style={value.style}>
								{index + 1}. {value.label}
							</Link>
						</Typography>
					)}
				)}
			</div>
		</Dialog>
	);
}

Links.propTypes = {
	onClose: PropTypes.func.isRequired,
	open: PropTypes.bool.isRequired,
};