import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';

import Dialog from '@material-ui/core/Dialog';




const useStyles = makeStyles((theme) => ({
  root: {
    margin: "2px 10px 1px 10px",
    fontSize: "16pt"
  },
  container: {
    marginLeft: "40vw"
  }
}));

export default function GlobalInfo(props) {
  const { onClose, open } = props;
  const classes = useStyles();

  const handleClose = () => {
    onClose();
  };

  return (
  <Dialog onClose={handleClose} className={classes.container} aria-labelledby="simple-dialog-title" open={open} style={{ zIndex: 2002 }}>
  <div  style={{
     backgroundColor: "#FAEBD7"
  }}>
    <Typography className={classes.root}>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Все без преувеличения исследователи называют Святск лучшим образцом дворцовой архитектуры переходного типа от барокко к классицизму, причем, не только в Гродненской области, но и в Беларуси. Чтобы вы себе лучше представляли: барокко – это вычурный и пышный архитектурный и скульптурный стиль XVI—XVIII вв, а классицизм – это художественный стиль, который отражает естественное стремление человека к красоте, целостности, простоте и ясности содержания и формы.
      Ныне Святск - это небольшая деревня близ Гродно.
    </Typography>
    <Typography className={classes.root}>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Ранее, это поместье – родовое гнездо Воловичей. Воловичи были очень влиятельным родом в течении почти трех веков. С тех пор, когда первый из них спас канцлера от стрел.  Отсюда на гербе Воловичей стрелы. Звали его Вол, отсюда и пошла фамилия Воловичи. Они всегда были при королевском дворе: служили послами, судьями, епископами, были просветителями и меценатами.
    </Typography>
    <Typography className={classes.root}>        
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Однажды Станислав Август Понятовский – король Речи Посполитой, отправился во Францию. А в то время, надо сказать, для того, чтобы укрепить свою власть он опирался на известные роды (Радзивилы, Сапеги, Воловичи). 
      Юзеф Волович был особенно близок королю Речи Посполитой. Король ценил его патриотизм, преданность и, конечно, деньги. Ю.Волович служил при королевском дворе и владел на Гродненщине большими поместьями и часто сопровождал Понятовского в поездках за границу в Европу.
    </Typography>
    <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Путешествие в Париж не прошло для короля даром. Там он не только обновил свой гардероб, но и набрался новых впечатлений. Больше всего ему понравился роскошный Версальский дворец. И он решает построить себе такой же. 
    </Typography>
    <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Проект дворца предложили сделать молодому итальянскому архитектору Джузеппе Сакко. Итальянские архитекторы в ту пору считались лучшими в Европе. А Дж.Сакко, к тому времени приобрел себе большую известность в Речи Посполитой и к тому же на тот момент находился на территории Гродненщины. Это он при А.Тизенгаузе - политическом и общественном деятеле Великого княжества Литовского, проектировал дворцы на Городнице, в Каменце, Щорсах, Бресте, реставрировал Новый замок в Гродно. И Джузеппе взялся за эту работу.
    </Typography>
    <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Но первым увидел проект не король, а гродненский магнат Юзеф Волович. Надо сказать, что двор «Святск» не сразу стал поместьем Воловичей. Только через 200 лет Волович сумел полностью выкупить эти земли, чтобы устроить здесь свое родовое гнездо.
      Проект замка так понравился магнату, что он решил действовать: отправился в Варшаву к королю и уговорил продать ему проект дворца. Король согласился. Он ценил своего придворного, его преданность и деньги. И буквально через пару месяцев в местечке Святск началось грандиозное строительство. 
    </Typography>
    <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Джузеппе Сакко разработал не только проект здания, но и эскизы интерьера внутреннего убранства дворца. Итальянец из Вероны, очень тонко учтя ландшафт и пейзаж местности, совместил два архитектурных стиля – барокко и классицизм – в одном здании. Причем, внутренняя архитектура дворца была богаче наружной. Здесь архитектор отказался от простых решений и сделал залы и кабинеты разных форм и размеров. Часть комнат была оформлена в «римском» стиле, другая – в «античном»; большой зал с прекрасным видом на парк назывался «романтическим». В комнатах – богатая роспись, камины, печи из ценного печного изразца. 
      
      Джузеппе также контролировал и все строительство в местечке. Жизнь Дж.Сакко тесно переплелась с этим местом под названием Святск. Там он и встретил свою любовь, девушку своей мечты. Звали ее Франтишка.  Они поженились. Венчались в Гродно в Бернардинском костеле. А затем переехали в имение, которое подарил Джузеппе сам король Понятовский.
      Но сам Юзеф не смог увидеть дворец во всем его великолепии. Он скончался в 1779 году от разрыва сердца. При жизни Юзефа была построена лишь центральная его часть. 
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Завершалось строительство Святского дворца уже при его сыне Антонии в начале XIX века. По обе стороны центрального здания появились боковые флигеля и галереи-колоннады, соединившие постройки в единый комплекс. Возведенный в стиле барокко, он выглядел шикарно. В то время это был один из самых роскошных замков в Западной Беларуси (люстры, камины, дорогая мебель).
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;После раздела Речи Посполитой эти земли стали частью Прусской империи, и канцлер, задабривая местную элиту, наградил Антония титулом графа. 
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;В XIX веке Антоний Волович передал имение своему наследнику Михаилу. Его дочь Ядвига позже стала последней владелицей дворцового ансамбля из рода Воловичей. Потом владельцы менялись: в 1909 году упоминается мещанин Абрам Мерецкий. Был еще один владелец — Гурский. Но у него оказалось много долгов, поэтому дворец забрало государство. Дворец сильно пострадал в годы Первой мировой войны. Его грабили и разрушали, из него вынесли практически все вещи. В 1921 году, когда объект отошел Польше, там находился пансионат для людей, которые страдают наркотической зависимостью. В 20-30-е годы прошлого века дворцовый ансамбль, тогда лишь отдаленно напоминающий о былом величии, перешел в собственность государства. После реконструкции в 1930-х здесь разместился наркологический санаторий, с 1946-го – госпиталь для инвалидов Великой Отечественной войны, а с 1971-го по 2005-й – санаторий для больных туберкулезом. 
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;В 2009-м дворцово-парковый комплекс передан ОАО «Санаторий «Озерный». Надо заметить, что в мире всего два подобных замка: в Версале и у нас. И если французский брат-близнец, как говорится, живет и здравствует, красуясь перед взорами многочисленных туристов, то его белорусский аналог постепенно рассыпается на глазах до недавнего времени. Но новый хозяин взялся восстановить исторический облик поместья, каким оно было при Воловичах. И в настоящее время идет реконструкция этого прекрасного места.
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;В Беларуси много дворцов, но такой, как Святский – один. 
    </Typography>
    <Typography className={classes.root}>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Сам дворец и различные хозяйственные постройки расположены на очень живописной местности, и буквально окутаны со всех сторон прудами и каналами. 
      В атмосфере трех залов, сохранивших более чем двухсотлетний декор изящность и изысканность потолочной лепнины, узорчатые своды колонн и каменных печей напоминают о былом великолепии, и невольно представляется шуршание пышных дамских платьев. Каждый зал был оформлен в неповторимом стиле. Зал в романтичном стиле – с пейзажами на стенах и небом на потолке, зал древнегреческий – с барельефами из жизни древних греков, колонный зал, в основе которого колонны с узорчатыми сводами, каменные печь и камин… Все залы выходят в вестибюль первого этажа напротив главного входа. Свой первоначальный вид сохранила и парадная двухмаршевая лестница, которая некогда была главным элементом дворцового вестибюля. Стены вокруг нее украшала живопись с военными сценами. Тем самым с первого этажа гостей замка ступени вели не только в парадные залы второго, но словно и в историю рода Воловичей, о воинской доблести которых напоминает родовой герб с изображением стрел.
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;В последнее время перед реставрацией в этих залах располагалась библиотека, читальный зал и танцевальный зал с бильярдной. В боковых флигелях располагались кинозал и столовая. Но в то время это были уже остатки былого великолепия.
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Рядом с дворцом Воловичи построили семейную часовню. В советское время здесь была почта, но позже это снова место молитвы – раз в неделю сюда приезжал священник и проводил богослужение. Так было до недавнего времени. В часовне случился пожар. Сейчас она закрыта и ждет реставрации.
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Рядом с поместьем располагается хоздвор, в который входили хозпостройки из цельного камня и красного кирпича, а к ним примкнула, словно обнявшись, деревенька, в которой проживают люди, работавшие в бывшем санатории «Святск». Также возле дворца с одной и другой стороны разместились сады. В данный момент остался только один и тот наполовину поредел в связи со строительными работами. 
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Вокруг дворца был разбит замечательный парк, обсаженный тенистыми ясеневыми и тополиными аллеями, с искусственными озерами, связанными между собой судоходным шлюзом. Одно из них, находящееся прямо напротив дворца, было кристально чистое – дно его выложили дубовыми досками, и территория у некогда пышного дворца в пригороде Гродно была близка к картинкам Венеции – гости замка плавали на лодках, вдали от городской суеты отдыхали на лоне природы. За самим дворцом располагался еще один красивый парк, который с течением времени превратили в спортивную площадку для отдыхающих. 
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;За третьим озером (так называют его местные жители) располагается небольшой лесопарк с редкими смешанными деревьями. В нем очень красиво, особенно весной, когда вся земля покрывается сплошным бело-желтым благоухающим ковром из весенних цветов. На окраине леса рос могучий сторожил-тополь. Сколько ему было лет, не знает никто. Но как ни силен был тополь, а годы брали свое, и в один из ненастных дней налетел сильный ветер и свалил великана. На фото можно увидеть всю мощь богатыря-дерева. Но бороться со стихией не может никто…
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Эти чудесные места облюбовали не только люди, но и животные. Кого только можно ни встретить возле озер и в лесу… Это и бобры, которые свалили не одно дерево для строительства своих хаток, и выдры, которые питаются не только рыбой, но также не брезгуют птенцами водоплавающих птиц, которых в избытке на озерах. Здесь поселились аисты, лебеди и утки, которые каждый год выводят птенцов, можно встретить как серую, так и белую цаплю. Иногда их можно увидеть и в феврале, когда земля укрыта снегом и скована морозом. В лесу множество птиц: скворцы, сойки, трясогузки, поползни, дятлы, синицы, воробьи, вороны, голуби, также обитают и хищные птицы: ястреб и филин. Еще в лесу живут белки и лисы. Иногда заходят косули и даже дикие кабаны.
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Граф Юзеф не зря влюбился в этот райский уголок, т.к. он не просто красив, но и защищен со всех сторон густыми деревьями и кустарниками от дороги.
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Но какой же дворец без привидения?! В этом плане родовое имение в Святске может посоперничать с замками в Гольшанах и Лиде. Местные жители из поколения в поколение передают легенду о любви юной панночки к конюху. Разгневанный пан Волович, узнав, что его дочь ждет ребенка от простолюдина, приказал утопить его в пруду, а родную дочь живьем замуровать в одном из залов. Шаги панны якобы до сих пор слышатся в огромном опустевшем доме. – Немало наслышана о привидениях, – говорит Тереза Гедо, которая много лет работала в санатории «Святск» и была последним главным врачом этой здравницы. – Рассказывали, будто в первые послевоенные годы, еще когда здесь располагался госпиталь, один врач видел призрак девушки в голубом одеянии, летающий сквозь стены. Намного позже испуганные медсестры рассказывали, что после отбоя слышали, как в бильярдной, закрытой на ключ, перекатываются шары. Они вошли туда, сложили шары в треугольник, отдельно поставили кии. Через некоторое время опять услышали звук шаров в бильярдной. Заглянули, открыв запертую дверь ключом, а шары и кии разбросаны. 
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;У озера, которое облюбовала лебединая пара, узкая тропинка ведет вдоль необычной аллеи. В нескольких местах деревья, будто обнявшись, срослись в парах друг с другом. Местные жители называют это дерево – деревом любви. Между его стволами была прибита лавочка. На ней любили посидеть молодые заезжие пары. Говорят, что если посидишь под деревом любви, отношения станут еще крепче. А еще поговаривают в здешних краях и о необычной силе привезенных с окрестных полей валунов.
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Известен Святск не только в Беларуси, но и за ее границами. Сюда не раз приезжали снимать кино: «С юбилеем подождем», «Смерш», «Застава Жилина», «Без права на ошибку». 
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Пейзажи парка и фасады дворцового комплекса манили для фотосессий молодоженов в день главного торжества. Очень символично у озера их встречала почти ручная лебединая пара, а с крыши дворца клекотом и широким размахом крыльев сулила удачу пара аистов, устроившая здесь гнездо.
      </Typography>
      <Typography className={classes.root}> 
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;С 2017 г здесь ведутся реставрационные работы. Ни шум строительной техники, ни ежедневная суета людей не пугают птиц и сегодня. В первые дни весеннего тепла громким криком встретили нынешних реставраторов они в этом году. Если верить приметам и не только, он станет очередной счастливой вехой для дворцово-паркового комплекса Воловичей, который спустя почти два с половиной столетия после постройки переживает новый период – возрождение…  
    </Typography>
    </div>
  </Dialog>
  );
}

GlobalInfo.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
};