import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';

import Dialog from '@material-ui/core/Dialog';




const useStyles = makeStyles((theme) => ({
  root: {
    '& > * + *': {
      marginLeft: theme.spacing(2),
    },
    margin: "2px 10px 1px 10px",
    fontSize: "16pt"
  },
  container: {
    marginLeft: "40vw"
  }
}));

export default function GuestInfo(props) {
  const { onClose, open } = props;
  const classes = useStyles();

  const handleClose = () => {
    onClose();
  };

  return (
  <Dialog onClose={handleClose} className={classes.container} aria-labelledby="simple-dialog-title" open={open} style={{ zIndex: 2002 }}>
  <div  
        style={{
          backgroundColor: "#FAEBD7"
        }}s>
    <Typography className={classes.root}>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Рады приветствовать Вас, дорогой гость!
    </Typography>
    <Typography className={classes.root}>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Приглашаем совершить виртуальный тур по дворцово-парковому ансамблю Воловичей (д.Святск).
    </Typography>
    <Typography className={classes.root}>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Мы предлагаем вам:
    </Typography>
    <Typography className={classes.root}>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;- просмотр материалов в полноэкранном формате в самостоятельно выбранном темпе (перещёлкиванием слайдов);
    </Typography>
    <Typography className={classes.root}>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;- аудиогид (прослушивание по запросу, нажав кнопку "микрофон");
    </Typography>
    <Typography className={classes.root}>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;- историческую справку по отдельным архитектурным объектам (кнопка "информация");
    </Typography>
    <Typography className={classes.root}>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;- посещение отдельных объектов тура из галереи;
    </Typography>
    <Typography className={classes.root}>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;- возможность совершать тур с любого технического устройства (компьютер, планшет, мобильный телефон);
    </Typography>
    <Typography className={classes.root}>
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;- словарь основных понятий.
    </Typography>
    </div>
  </Dialog>
  );
}

GuestInfo.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
};